import { Component, OnInit, OnDestroy } from '@angular/core';
import { DemoService } from '../services/demo.service';
import { ActivatedRoute, Params } from '@angular/router';
import { Subscription } from 'rxjs/Subscription'; // With unsubscribe

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent {
  title: string;
  user: { id: number, name: string };
  paramsSubscription: Subscription;

  constructor(private demoService: DemoService, private route: ActivatedRoute) { }
  ngOnInit() {
    //this.title = this.demoService.someMethod();
    // Get Query and fragment Paramter
    console.log(this.route.snapshot.queryParamMap);
    console.log(this.route.snapshot.fragment);
    
    this.route.queryParams.subscribe();
    this.route.fragment.subscribe();

    // Show Direct Value
    this.user = {
      id: this.route.snapshot.params['id'],
      name: this.route.snapshot.params['name']
    }
    // After change route param
    // Without unsubscribe
    // this.route.params
    //   .subscribe(
    //     (params: Params) => {
    //       this.user.id = params['id'],
    //       this.user.name = params['name'];
    //     }
    //   );

    this.paramsSubscription = this.route.params
      .subscribe(
        (params: Params) => {
          this.user.id = params['id'],
          this.user.name = params['name'];
        }
      );
  }
  private ngOnDestroy() {
    this.paramsSubscription.unsubscribe();
  }
  //   order = "name";
  // ascending = true;

  editFrm = true;
  employees = [
    { name: "Sikandar", position: "Programmer" },
    { name: "Alex", position: "Designer" },
    { name: "Maria", position: "Manager" }
  ];
  model: any = {};
  model2: any = {};
  msg: any = "";
  names = "dileep";
  addEmployee() {
    this.employees.push(this.model);
    console.log("this.employees", this.employees)
    this.model = {};
    this.msg = "Record is successfully added..... ";
  }
  deleteEmployee(i) {
    this.employees.splice(i, 1);
    this.msg = "Record is successfully deleted..... ";

  }
  clickMe() {
    let self = this;
    setTimeout(function () {
      self.msg = "";
    }, 2000)

  }
  myValue;
  editEmployee(k) {
    this.editFrm = false;
    this.model2.name = this.employees[k].name;
    this.model2.position = this.employees[k].position;
    this.myValue = k;

  }
  updateEmployee() {
    let k = this.myValue;
    for (let i = 0; i < this.employees.length; i++) {
      if (i == k) {
        this.employees[i] = this.model2;
        this.model2 = {};
        this.msg = "Record is successfully updated..... ";
        this.editFrm = true;
      }

    }

  }



}



