import { Component, OnInit } from '@angular/core';
import { DirectoryService } from '../services/directory.service';
//import * as $ from 'jquery'; 
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import ExcelRecords from './excelRecord';
import { CookieService } from 'ngx-cookie-service';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs/Observable';
import {startWith} from 'rxjs/operators/startWith';
import {map} from 'rxjs/operators/map';

@Component({
  selector: 'app-directory',
  templateUrl: './directory.component.html',
  styleUrls: ['./directory.component.css']
})
export class DirectoryComponent implements OnInit { 
   myControl: FormControl = new FormControl();
  public popoverTitle: string = 'Delete confirmation';
  public popoverMessage: string = 'Are you sure you want to delete the contact?';
  public confirmClicked: boolean = false;
  public cancelClicked: boolean = false;

  editProfile : boolean; 
  formHeader : string;
  closeResult: string;
  dirUserData :any = [];
  dataDirectory = {};
  directory = {};
  customData = {};
  excelCustomRecord = {};
  excelDataArray = [];
  directoryRequests = [];  

  constructor( 
    private dirService: DirectoryService,
    private modalService: NgbModal,
    private cookieService: CookieService
  ) { 
    this.editProfile = false;
    this.formHeader = "Add Directory";    
      this.dataDirectory = {
        "clientUserId": 5414029078036480,
        "pageNumber": 1,
        "pageSize": 10,
      } 
  }

  options = [
    'One',
    'Two',
    'Three'
  ];
  
  getDirectoryUser(){
    this.dirService.getDirectoryUser(this.dataDirectory).
    subscribe(     
      (resultArray)=>{
        const list = resultArray['list']; 
        this.dirUserData = list;        
      },err => {
        console.log("Error occured");
      })    
  }

  addCustomExcel(directory: any){
    this.customData = {
      "email": this.directory['email'],
      "firstName": this.directory['firstName'],
      "lastName": this.directory['lastName'],
      "businessCategory": this.directory['businessCategory'],
      "address": this.directory['address'],
      "companyName": "",     
      "groupName": this.directory['groupName'],
      "mobile":""            
    };  
    
    this.excelDataArray.push(this.customData);
    this.excelCustomRecord['clientUserId'] = 5414029078036480;
    this.excelCustomRecord['directoryRequests'] = this.excelDataArray;
    
    this.dirService.addCustomExcel(this.excelCustomRecord).
    subscribe(
      (resultArray)=>{   
        if(resultArray['statusCode'] === 201){ 
          this.directory = '';
          alert("Data Added successfully");
          this.getDirectoryUser();        
        }
      },err=>{
        console.log("Error Occured");
      })       
  }

  /** Start autocomplete search from here ***/
  businessCategory:any = [];
  getOtherCategory(){
      const URL = "http://103.76.253.133:8752/api/v1/user/category/6421608541454336";
      this.dirService.getEventTemplate(URL).subscribe((response)=>{
        this.businessCategory = response['list'];
        console.log("businessCategory", this.businessCategory)
      });
  }
 // console.log("this.businessCategory", this.businessCategory);
  filteredOptions: Observable<string[]>;

  filter(val: string): string[] {
    console.log("val", val)
    return this.businessCategory.filter(option =>
    option.subCategory.indexOf(val) === 0);
  }

  ngOnInit(): void {  
    this.getDirectoryUser();
    this.getOtherCategory();
    // $('button').click(function(){ //console.log("Jquery Is working") // })
    console.log("this.myControl.valueChanges", this.myControl.valueChanges)
    this.filteredOptions = this.myControl.valueChanges.map(val => this.filter(val));
    // .pipe(
    //   startWith(''),
    //   map(val => this.filter(val))
    // );
  }  

  onOptionSelect(val) {
    console.log("val=",val)
  }
  openAddDirModal(content) {
    console.log("content", content)
    this.modalService.open(content).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      console.log("this.closeResult", this.closeResult)
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  editCustomExcelData(directory : any){
    console.log("directory", directory)
    this.formHeader = "Update Directory";
    this.editProfile = true;
    console.log("editCustomExcelData", directory);
    this.directory = directory;
  }

  updateCustomExcelData = function(directory){
    this.dirService.updateCustomExcelData(directory).subscribe(Response=>
      {
        if(Response.statusCode === 200){
          alert("Data Upadted successfull");
          console.log("bbbb")
        }        
      },err=>{
        alert("error occured");
      });
  }

  deleteUser = function(did){
    this.dirService.deleteUser(did).subscribe(response =>
      {
        if(response.statusCode === 200){
          alert("Deletes successfully");
          this.getDirectoryUser(); 
        }
      },err=>{
        alert("error Occured");
      }
    )
  }

}



