import { BrowserModule } from '@angular/platform-browser';
import { ConfirmationPopoverModule } from 'angular-confirmation-popover';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {FormsModule,ReactiveFormsModule, FormGroup} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {ToasterModule, ToasterService, ToasterConfig} from 'angular2-toaster';
import {NgPipesModule} from 'ngx-pipes';
import { Ng2OrderModule } from 'ng2-order-pipe';
//import { OrderModule } from 'ngx-order-pipe'; // Angular 4+
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import {HttpModule} from '@angular/http';
//import {HttpInterceptor} from "./shared/http-interceptor";
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { CookieService } from  'ngx-cookie-service';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { ngfModule, ngf } from "angular-file";
import {MatAutocompleteModule, MatFormFieldModule, MatInputModule} from '@angular/material';
import { FileSelectDirective, FileDropDirective } from 'ng2-file-upload';
import { ImageCropperModule } from 'ngx-image-cropper';


import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { DropdownDirective } from './shared/dropdown.directive';
import { RainbowDirective } from './shared/rainbow.directive';
import { UserProfileComponent } from './user-profile/user-profile.component';
import { ParentComponent } from './parent/parent.component';
import { ChildComponent } from './child/child.component';
import { LoginComponent } from './login/login.component';
import { FooterComponent } from './footer/footer.component';
import { CategoryComponent } from './category/category.component';
//import { CategoryPipe } from './filter/category.pipe';
import { OrderrByPipe } from './filter/orderby.pipe';
import { UserService } from './services/user.service';
import { PeopleService } from './services/people.service';
import { DemoService } from './services/demo.service';
import { PagerService } from './services/pager.service';
import { CockpitComponent } from './cockpit/cockpit.component';
import { ServerElemnetComponent } from './server-elemnet/server-elemnet.component';
import { PaginationComponent } from './pagination/pagination.component';
import { DirectoryComponent } from './directory/directory.component';
import { DirectoryService } from './services/directory.service';
import { AuthInterceptor } from './services/auth.interceptor';
import { UploadImageComponent } from './upload-image/upload-image.component';



const appRoutes: Routes = [ 
  { path: '', component: LoginComponent },
  { path: 'userDetail/:id/:name', component: UserProfileComponent },
  { path: 'parent', component: ParentComponent },
  { path: 'child', component: ChildComponent },
  { path: 'category', component: CategoryComponent },
  { path: 'input', component: ServerElemnetComponent },
  { path: 'output', component: CockpitComponent },
  { path: 'pagination', component: PaginationComponent },
  { path: 'directory', component: DirectoryComponent },
   { path: 'uploadImage', component: UploadImageComponent },
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    DropdownDirective,
    RainbowDirective,
    UserProfileComponent,
    ParentComponent,
    ChildComponent,
    LoginComponent,
    FooterComponent,
    CategoryComponent,
   // CategoryPipe,
    OrderrByPipe,
   CockpitComponent,
   ServerElemnetComponent,
   PaginationComponent,
   DirectoryComponent,
   UploadImageComponent, 
   FileSelectDirective,    
  ],
  imports: [
    BrowserModule,   
    RouterModule.forRoot(appRoutes),
    FormsModule,
    ReactiveFormsModule,
    ToasterModule,
    BrowserAnimationsModule,
    NgPipesModule,
    //OrderModule, // Angular 4+
    Ng2OrderModule,
    Ng2SearchPipeModule,
    HttpModule,
    HttpClientModule ,
    MatAutocompleteModule,
    MatFormFieldModule,
    MatInputModule,
    NgbModule.forRoot(),
    ConfirmationPopoverModule.forRoot({
      confirmButtonType: 'danger' // set defaults here
    }),
    ngfModule,
    ImageCropperModule,
  ],
  providers: [UserService, PeopleService, PagerService, DemoService, CookieService, DirectoryService, { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true }],
  bootstrap: [AppComponent]
})
export class AppModule {
 }



