
import { Injectable } from '@angular/core';
//import { Http, Response } from '@angular/http';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import { CookieService } from 'ngx-cookie-service';


@Injectable()
export class DemoService {
    _url: string;
    constructor(private http: Http, private cookieService: CookieService) { }

    uploadFile(data: any): Observable<{}> {
        let cookiesData = this.cookieService.get('userDetail');
        let userData = JSON.parse(cookiesData);
        let head = new Headers({'Authorization':"bearer "+userData.access_token});
        let options = new RequestOptions({ headers: head });
        this._url = 'http://103.76.253.133:8752/api/v1/cloud/aws/upload';
        return this.http.post(this._url, data, options)
            .map(this.handleData)
            .catch(this.handleError);
    }

    private handleData(res: Response) {
        let data = res.json();
        return data;
    }
    private handleError(error: Response | any) {
        return Observable.throw('API failed');
    }
}
